.tituloIncidencia{
    padding-top: 180px;
    padding-bottom: 50px;
    margin-right: 18%;
}

textarea{
    width: 40%;
    height: 200px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 5px;
    background-color: #f8f8f8;
    resize: none;
    margin-bottom: 30px;
}

.redSizeSmall, .green, .redSizeBig {
    border: none;
    border-radius: 10px;
    padding: 15px;
    color: white;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    position: relative;
    text-align: center;
}

.redSizeSmall{
    background-color: #9c1414;
    width: 10vw;
    margin-right: 15px;
}
.redSizeSmall:hover{
    background-color: brown;
}

.green{
    background-color: darkgreen;
    font-size: 20px;
    width: 10vw;
}
.green:hover{
    background-color: #00b559;
}


